//date-inputs.js
export default class DateInputs {
    constructor(options){
        this.inputs = document.querySelector('#inputs-template').content.cloneNode(true);
        this.inputs.querySelector('.add-previous-date-button').addEventListener('click', this.addPreviousDateHandler);
        if(!options.previousDate){
            this.inputs.querySelector('.add-previous-date-button').remove();
        }
        this.inputs.querySelector('.add-date-button').addEventListener('click',this.addDateHandler);
        this.inputs.querySelector('.remove-date-button').addEventListener('click', this.removeDateHandler);
        if(!options.removeDate){
            this.inputs.querySelector('.remove-date-button').remove();
        }
        this.inputs.querySelector('.app-interface__till-present').querySelector('input').addEventListener('change', this.tillPresentHandler);
        if(!options.tillPresent){
            this.inputs.querySelector('.app-interface__till-present').remove();
        }
    }
    render(){
        return this.inputs;
    }
    static renderLastInputs(){
        return new this({
            previousDate: false,
            removeDate: true,
            tillPresent: true
        });
    }
    static renderFirstInputs(){
        return new this({
            previousDate: true,
            removeDate: true,
            tillPresent: false
        });
    }
    static renderOnlyInputs(){
        return new this({
            previousDate: true,
            removeDate: false,
            tillPresent: true
        });
    }
    static renderInitialInputs(){
        return new this({
            previousDate: false,
            removeDate: false,
            tillPresent: true
        });
    }
    static renderMiddleInputs(){
        return new this({
            previousDate: false,
            removeDate: true,
            tillPresent: false
        });
    }

    removeDateHandler(evt){
        evt.preventDefault();
        if(!inputsWrapper.nextElementSibling){
            inputsWrapper.previousElementSibling.appendChild(inputsWrapper.querySelector('.app-interface__till-present').cloneNode(true));
        }
        this.inputs.remove();
    }

    addDateHandler(evt){
        evt.preventDefault();
        const inputsWrapper = this.closest('.inputs-wrapper');
        if(!inputsWrapper.nextElementSibling && !inputsWrapper.previousElementSibling){
            const inDate = inputsWrapper.querySelector('input[name="in-date"]').value;
            const outDate = inputsWrapper.querySelector('input[name="out-date"]').value;
            inputsWrapper.after(DateInputs.renderLastInputs());
            const newInputsWrapper = inputsWrapper.parentNode.replaceChild(DateInputs.renderFirstInputs(), inputsWrapper);
            console.log(newInputsWrapper);
            newInputsWrapper.querySelector('input[name="in-date"]').textContent = inDate;
            newInputsWrapper.querySelector('input[name="out-date"]').textContent = outDate;
        } else{
            if(document.querySelectorAll('.inputs-wrapper').length > 1 && !document.querySelector('.inputs-wrapper').querySelector('.add-previous-date-button')){
                document.querySelector('.inputs-wrapper').appendChild(this.previousDate.cloneNode(true));
            }
            this.inputs.insertAdjacentElement('afterend', DateInputs.renderMiddleInputs());
        }
    }
    addPreviousDateHandler(evt){
        evt.preventDefault();
        this.inputs.insertAdjacentElement('beforebegin', DateInputs.renderFirstInputs());
        this.previousDate.remove();
    }
    tillPresentHandler(){
        this.addDate.disabled = true;
    }
}